import React, { Component } from 'react';
import { BrowserRouter, Route, Redirect, Switch } from 'react-router-dom';

import SideBar from './components/Sidebar/Sidebar';
import PortfolioPage from './components/Pages/Portfolio/portfolio';
import AboutPage from './components/Pages/About/about';
import './App.css';

class App extends Component {
  state = {
    sideDrawerOpen: true
  };

  drawerToggleClickHandler = () => {
    this.setState((prevState) => {
      return {sideDrawerOpen: !prevState.sideDrawerOpen};
    })
  };

  render() {
    let mainContent = "main-content";

    // fix from here
    if(this.state.sideDrawerOpen){
      mainContent = "main-content-push-left";
    }

    return (
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <React.Fragment>
          <SideBar show={this.state.sideDrawerOpen} />
          <main className={mainContent}>
            <Switch>
              <Redirect path={process.env.PUBLIC_URL + '/'} to="/about" exact/>
              <Route path="/about" render={(props) => <AboutPage {...props} drawerClickHandler={this.drawerToggleClickHandler}/>}/>
              <Route path="/portfolio" render={(props) => <PortfolioPage {...props} drawerClickHandler={this.drawerToggleClickHandler} />}/>
            </Switch>
          </main>
        </React.Fragment>
      </BrowserRouter>
    );
  }
}

export default App;
